import React, { useState, useEffect } from "react";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import axios from "axios";
/* Importing the useDispatch hook from the react-redux library. */
import { useDispatch } from "react-redux";
/* Importing the createUsers function from the asyncActions file. */
import { authorization } from "../State/asyncActions";

function Login() {
  const dispatch = useDispatch();
  const [validated, setValidated] = useState(false);
  const [input, setinput] = useState({});
  const [users, setusers] = useState([]);
  const [me, setme] = useState([]);
  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setinput((values) => ({ ...values, [name]: value }));
  };
  const handleSubmit = (event) => {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }
    setValidated(true);
    event.preventDefault();

    /* Creating an object with the name, email, and password properties. */
    let data = {
      name: input.name,
      email: input.email,
      password: input.pass,
    };
    /* Dispatching the createUsers action to the Redux store. */
    dispatch(
      authorization([
        data,
        "https://deafening-bells-production-7c8c.up.railway.app/api/auth/login",
      ])
    );
  };
  useEffect(() => {
    const getusers = async () => {
      try {
        // https://deafening-bells-production.up.railway.app/
        const res = await axios.get(
          "https://deafening-bells-production-7c8c.up.railway.app/api/auth/getusers",
          {
            withCredentials: true,
          }
        );
        const usersFromServer = await res.data;
        setusers(usersFromServer);
      } catch (error) {
        console.log(error);
      }
    };
    getusers();
  }, []);

  const handlegetme = async () => {
    try {
      let res = await axios.get(
        "https://deafening-bells-production-7c8c.up.railway.app/api/auth/profile",
        {
          withCredentials: true,
        }
      );
      let data = await res.data;
      if (res.status === 200) {
        setme(data);
      }
    } catch (err) {
      setme(err.response.data.error);
    }
    console.log(me);
  };
  const logout = async () => {
    try {
      let res = await axios.get(
        "https://deafening-bells-production-7c8c.up.railway.app/api/auth/logout",
        {
          withCredentials: true,
        }
      );
      let data = await res.data;
      if (res.status === 200) {
        setme(data);
      }
    } catch (err) {
      setme(err.response.data.error);
    }
  };
  return (
    <div className="container mt-5">
      <Form
        className="mb-3"
        noValidate
        validated={validated}
        onSubmit={handleSubmit}
      >
        <Row className="d-flex flex-column gap-3 mb-3">
          <Form.Group as={Col} md="4" controlId="validationCustom01">
            <Form.Label>Email</Form.Label>
            <Form.Control
              required
              type="email"
              placeholder="example@gmail.com"
              name="email"
              value={input.email || ""}
              onChange={handleChange}
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} md="4" controlId="validationCustom02">
            <Form.Label>Password</Form.Label>
            <Form.Control
              required
              type="password"
              placeholder="Password"
              name="pass"
              value={input.pass || ""}
              onChange={handleChange}
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
        </Row>
        <Button type="submit">Submit form</Button>
      </Form>
      <Row>
        <div className="col">
          <table className="table">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">First</th>
                <th scope="col">Email</th>
              </tr>
            </thead>
            <tbody>
              {users &&
                users.map((item, index) => {
                  return (
                    <tr key={index}>
                      <th scope="row">{index + 1}</th>
                      <td>{item.name}</td>
                      <td>{item.email}</td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </div>
      </Row>
      <button className="btn btn-sm btn-primary" onClick={handlegetme}>
        get me
      </button>
      <h1>{me.email}</h1>

      <button className="btn btn-sm btn-primary" onClick={logout}>
        logout
      </button>
    </div>
  );
}

export default Login;
