import React, { useState, useEffect } from "react";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import axios from "axios";
/* Importing the useDispatch hook from the react-redux library. */
import { useDispatch, useSelector } from "react-redux";
/* Importing the createUsers function from the asyncActions file. */
import { authorization } from "../State/asyncActions";
// import img from './duck.jpg'

function Signup() {
  let user = useSelector((state) => state.user);
  /* A hook that is used to dispatch actions to the Redux store. */
  const dispatch = useDispatch();
  /* A React Hook that is used to set the state of the validated variable to false. */
  const [validated, setValidated] = useState(false);
  /* A React Hook that is used to set the state of the input variable to an empty object. */
  const [input, setinput] = useState({});

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    /* A React Hook that is used to set the state of the input variable to an empty object. */
    setinput((values) => ({ ...values, [name]: value }));
  };
  const handleSubmit = (event) => {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }
    setValidated(true);
    event.preventDefault();
    /* Creating an object with the name, email, and password properties. */
    let data = {
      name: input.name,
      email: input.email,
      password: input.pass,
    };
    /* Dispatching the createUsers action to the Redux store. */
    dispatch(
      authorization([
        data,
        "https://deafening-bells-production.up.railway.app/api/auth/signup",
      ])
    );
  };

  const [userd, setuserd] = useState({});

  useEffect(() => {
    axios
      .get(
        "https://deafening-bells-production.up.railway.app/api/auth/getusers",
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
      .then((res) => setuserd(res.data));
  }, []);
  // console.log(userd);

  return (
    <div className="container mt-5">
      <Form
        className="mb-3"
        noValidate
        validated={validated}
        onSubmit={handleSubmit}
      >
        <Row className="d-flex flex-column gap-3 mb-3">
          <Form.Group as={Col} md="4" controlId="validationCustom01">
            <Form.Label>Name</Form.Label>
            <Form.Control
              required
              type="text"
              minLength={3}
              autoComplete="username"
              placeholder="Name"
              name="name"
              value={input.name || ""}
              onChange={handleChange}
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} md="4" controlId="validationCustom02">
            <Form.Label>Email</Form.Label>
            <Form.Control
              required
              type="email"
              autoComplete="useremail"
              placeholder="example@gmail.com"
              name="email"
              value={input.email || ""}
              onChange={handleChange}
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} md="4" controlId="validationCustom03">
            <Form.Label>Password</Form.Label>
            <Form.Control
              required
              type="password"
              placeholder="Password"
              name="pass"
              minLength={8}
              autoComplete="current-password"
              value={input.pass || ""}
              onChange={handleChange}
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
        </Row>
        <Button type="submit">Submit form</Button>
      </Form>
      {userd ? (
        <ul>
          <li key={userd.id}>
            <p>{userd.name}</p>
            <p>{userd.email}</p>
            <img src={`${userd.image}&w`} width="200px" alt="profile" />
          </li>
        </ul>
      ) : null}
    </div>
  );
}

export default Signup;
