import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const initialState = {
  loading: false,
  auth_token: false,
  data: [],
};

export const authorization = createAsyncThunk(
  "user/authorization",
  ([data, api]) => {
    return axios
      .post(api, data, { withCredentials: true })
      .then((res) => {
        console.log(res.data);
      })
      .catch((err) => {
        console.log(err.message);
      });
  }
);

const userSlice = createSlice({
  name: "user",
  initialState,
  extraReducers: (builder) => {
    builder.addCase(authorization.pending, (state) => {
      state.loading = true;
    });
    builder.addCase(authorization.fulfilled, (state, action) => {
      state.loading = false;
      state.auth_token = true;
      state.data = action.payload;
    });
    builder.addCase(authorization.rejected, (state, action) => {
      state.loading = false;
      state.auth_token = false;
      localStorage.removeItem("auth-token");
      state.data = [];
    });
  },
});

export default userSlice.reducer;
