import React, { useState } from "react";

const Search = () => {
  const [search, setsearch] = useState("");
  const [data, setdata] = useState([]);
  const handleSearch = async (e) => {
    setsearch(e.target.value);
    let responce = await fetch(
      `https://deafening-bells-production.up.railway.app/api/auth/search/${search.toUpperCase()[0]}`,
      {
        method: "GET",
      }
    );
    let users = await responce.json();
    setdata(users);
  };
  const handleBlur = () => {
    setsearch("");
    setdata('')
  };
  return (
    <div className="container mt-5 p-3 d-flex justify-content-center align-items-center flex-column ">
      <form className="d-flex" role="search">
        <input
          className="form-control me-2 w-100"
          type="search"
          value={search}
          placeholder="Search"
          aria-label="Search"
          onChange={handleSearch}
          onBlur={handleBlur}
        />
        <button className="btn btn-outline-success" type="submit">
          Search
        </button>
      </form>
      <ul className="text-start w-25">
        {data.length
          ? data.map((user, i) => {
              return (
                <li key={i} className="w-100 fs-3 mt-2 p-1">
                  <p>{user.name}</p>
                </li>
              );
            })
          : null}
      </ul>
    </div>
  );
};

export default Search;
